.Toolbar {
    height: 50px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #2b304c;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav {
    height: 100%;
}

@media (max-width: 768px) {
    .Toolbar {
        justify-content: flex-start;
    }
    .DesktopOnly {
        display: none;
    }
}
