.SideDrawerToggle {
    width: 40px;
    height: 100%;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
    box-sizing: border-box;
    cursor: pointer;
}

.SideDrawerToggle div {
    width: 90%;
    height: 3px;
    background-color: white;
}

@media (min-width: 769px) {
    .SideDrawerToggle {
        display: none;
    }
}
